import areaselection from "@/components/actionbar/areaselection.vue"
const app = getApp();
export default{
	data(){
		return {
			show: false,
			value1:"",
			value2:"",
			value3:"",
			value4:"",
			statusBar:0,
			selectiondatalist:[],
			tokey:0,
			Addressoption:"",
			address:"",
			returntitleparameter:"",
			way:"",
			gid:"",
			cids:"",
			specname:"",
			num:"",
			img:"",
			storename:"",
			price:"",
			goodtitle:"",
			storeid:"",
			freight:""
		}
	},
	methods:{
		
		// Addressselection(){
		// 	this.show = true
		// },
		submits(){
			const _this = this
			let userphone = /^1([38][0-9]|4[579]|5[0-3,5-9]|6[6]|7[0135678]|9[89])\d{8}$/
			// console.log(this.value2.match(userphone))
			// console.log(this.value1,this.value2,this.value3,this.value4)
			if(this.value1 && this.value2.match(userphone)!==null && this.value4 && this.selectiondatalist.length>=3){
				//这是跳转上页
				// console.log(this.tokey)
				console.log(this.selectiondatalist)
				if(this.Addressoption=="newaddress"){
						uni.request({
						url:`${app.globalData.Requestpath}user/addShippingAddress`,
						method:"POST",
						data:{
							token:this.tokey,
							province:this.selectiondatalist[0][0].area_id,
							city:this.selectiondatalist[1][0].area_id,
							area:this.selectiondatalist[2][0].area_id,
							street_number:this.value4,
							postal_code:"",//这到明天需要改
							consignee_name:this.value1,
							consignee_phone:this.value2,
							is_default:0
						},
						success(res) {
							if(res.data.code==0){
								//pages/addressTo/addressTo?title=收货地址
								console.log(_this.way)
								if(_this.way==1){
									uni.navigateTo({
										url:`/pages/addressTo/addressTo?title=${_this.returntitleparameter}&gid=${_this.gid}&num=${_this.num}&way=${_this.way}&img=${_this.img}&storename=${_this.storename}&price=${_this.price}&goodtitle=${_this.goodtitle}&cids=${_this.cids}&storeid=${_this.storeid}&freight=${_this.freight}`
									})
								}else if(_this.way==2){
									uni.navigateTo({
										url:`/pages/addressTo/addressTo?title=${_this.returntitleparameter}&gid=${_this.gid}&specname=${_this.specname}&num=${_this.num}&way=${_this.way}&img=${_this.img}&storename=${_this.storename}&price=${_this.price}&goodtitle=${_this.goodtitle}&storeid=${_this.storeid}&freight=${_this.freight}`
									})
								}else{
									uni.navigateTo({
										url:`/pages/addressTo/addressTo?title=${_this.returntitleparameter}`
									})
								}
							}
						}
					})
				}else{
					uni.request({
						url:`${app.globalData.Requestpath}user/editShippingAddress`,
						method:"POST",
						data:{
							token:this.tokey,
							address_id:this.address,
							province:this.selectiondatalist[0][0].area_id,
							city:this.selectiondatalist[1][0].area_id,
							area:this.selectiondatalist[2][0].area_id,
							street_number:this.value4,
							postal_code:"",//这到明天需要改
							consignee_name:this.value1,
							consignee_phone:this.value2,
							is_default:0
						},
						success(res) {
							if(res.data.code==0){
								if(_this.way==1){
									uni.navigateTo({
										url:`/pages/addressTo/addressTo?title=${_this.returntitleparameter}&gid=${_this.gid}&num=${_this.num}&way=${_this.way}&img=${_this.img}&storename=${_this.storename}&price=${_this.price}&goodtitle=${_this.goodtitle}&cids=${_this.cids}&storeid=${_this.storeid}&freight=${_this.freight}`
									})
								}else if(_this.way==2){
									uni.navigateTo({
										url:`/pages/addressTo/addressTo?title=${_this.returntitleparameter}&gid=${_this.gid}&specname=${_this.specname}&num=${_this.num}&way=${_this.way}&img=${_this.img}&storename=${_this.storename}&price=${_this.price}&goodtitle=${_this.goodtitle}&storeid=${_this.storeid}&freight=${_this.freight}`
									})
								}else{
									uni.navigateTo({
										url:`/pages/addressTo/addressTo?title=${_this.returntitleparameter}`
									})
								}
							}else{
								app.globalData.showtoastsame("修改失败")
							}
						}
					})
				}
				//当点击的时候把值加入到把数据提交到数据库当中 在另一个页面进行数据的请求 渲染
			}else{
				uni.showToast({
					title:"请输入完整信息",
					mask:true,
					icon:"none"
				})
			}
			
		},
		//这是取消的
		Addresscancel(){
			uni.navigateBack({
			    delta: 1
			});
		},
		//这是用来接受子组件传过来的值---弹出地址的组件
		selectiondata(e){
			// console.log(e)
			this.selectiondatalist = e
		}
	},
	components:{
		areaselection,
	},
	onLoad(opction){
		this.returntitleparameter = opction.titleparameter
		this.Addressoption = opction.title
		this.address = opction.address
		this.statusBar = app.globalData.statusBar
		//1是购物车过来的
		//2是详情过来的
		console.log(opction.titleparameter)
		if(opction.titleparameter=='orderaddress'){
			let {way,gid,img,num,storename,price,goodtitle,storeid,freight} = opction
			this.way = way
			if(this.way==1){//这是购物车过来的
				let {cids} = opction
				this.cids = cids
			}else{//这是详情过来的
				let {specname} = opction
				this.specname = specname
			}
			this.gid = gid
			this.num = num
			this.img = img
			this.storename = storename
			this.price = price
			this.goodtitle = goodtitle
			this.storeid = storeid
			this.freight = freight
		}
	},
	
	created() {
		const _this = this
		uni.getStorage({
			key:"bindtokey",
			success(res){
				// console.log(res.data)
				_this.tokey = res.data
			}
		})
	}
}
